import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-new3',
  templateUrl: './new3.component.html',
  styleUrls: ['./new3.component.scss']
})
export class New3Component implements OnInit {
  
keyvord:any=""
historylist:any=[]
constructor() { }

  ngOnInit() {
  }
dosearch(){
  if(this. historylist.indexOf(this.keyvord)===-1 && this.keyvord!==""){
this. historylist.push(this.keyvord);
this.keyvord=""
}

}
empt(){
  this.historylist.splice(0)
}
delect(a){
  
  this.historylist.splice(a,1)
}

}
